import React from 'react';

//importacion de bootstrap
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';

import './App.css';
import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Start from './Components/Start'
import WebcamCapture from './Components/Camera'; 
import Instructions from './Components/Instructions';
import Form from './Components/Form';
import Finish from './Components/Finish';

function App() {
  return (
    <Router>
      <div className="App">
        <nav className="navbar navbar-light bg-light">
          <a className="navbar-brand" href="#">
            <img src={require("./images/logo.jpg")} width="80" height="80" className="d-inline-block align-top" alt=""/>
          </a>
          <p>San Vicente tiene futuro</p>
        </nav>
        <Switch>
          <Route exact path="/" component={Start}/>
          <Route exact path="/instrucciones" component={Instructions}/>
          <Route exact path="/formulario" component={Form}/>
          <Route exact path="/foto" component={WebcamCapture}/>
          <Route exact path="/final" component={Finish}/>
        </Switch>  
      </div>
    </Router>
  );
}

export default App;
