import React from "react";
import {Link} from "react-router-dom";

import Artyom from 'artyom.js';
const Jarvis = new Artyom();

class Finish extends React.Component {
  
    componentDidMount(){
        Jarvis.say("Felicitaciones "+ localStorage.getItem("name")+ ", Su trámite ya fué radicado con número 4857. En 3 días hábiles un funcionario del SISBEN visitará su casa.",{lang:"es-ES"})
    } 
    
    componentWillUnmount(){
        Jarvis.shutUp();
    }

    goHome(){
        let _this = this;
        Jarvis.shutUp();
        Jarvis.say("Muchas gracias por usar nuestro servicio virtual, esperamos la experiencia haya sido de su agrado.",{
            lang:"es-ES",
            onEnd:function(){
                _this.props.history.push('/');
            }
        })
        
    }

    render(){
        return (
            <div className="container-fluid vh-100">
                <h1>{"Felicitaciones "+ localStorage.getItem("name")}</h1>
                <p>Su trámite ya fué radicado con número 4857. En 3 días hábiles un funcionario del SISBEN visitará su casa. </p>

                <small id="ayudacedula" className="form-text text-muted mb-2">¿Cómo quiere recibir la constancia de su trámite?</small>
                <div className="container">
                    <button type="button" onClick={this.goHome.bind(this)} className="btn btn-primary col-sm-3 p-1 m-1">Mensaje de texto</button>
                    <button type="button" onClick={this.goHome.bind(this)} className="btn btn-success col-sm-3 p-1 m-1">Mensaje de WhatsApp</button>
                    <button type="button" onClick={this.goHome.bind(this)} className="btn btn-secondary col-sm-3 p-1 m-1">Imprimir comprobante</button>
                </div>        
            </div>      
        );
    }
};

export default Finish;