import React from "react";
import Webcam from "react-webcam";
import Gallery from 'react-grid-gallery';
import Background from "../images/camara.ico";

import Artyom from 'artyom.js';
const Jarvis = new Artyom();
 
const WebcamComponent = () => <Webcam />;

let IMAGES =[]
const INSTRUCCIONES = [
  {titulo: "Cédula frontal", texto: "Tome una fotografía del lado frontal de su cédula"},
  {titulo: "Cédula trasera", texto: "Tome una fotografía del lado trasero de su cédula"},
  {titulo: "Recibo de energía", texto: "Tome una fotografía de su último recibo de energía eléctrica"},
]
let contador = 0

class WebcamCapture extends React.Component {

  constructor (props, context){
    super(props, context);

    // Prepare simple state
    this.state = {
      imgSrc: <div>nada</div>,
      images:[],
      titulo:INSTRUCCIONES[contador].titulo,
      texto: INSTRUCCIONES[contador].texto,
      completed: false 
    };

    this.refreshState = this.refreshState.bind(this);
    
    // Load some commands to Artyom using the commands manager  
  }

  componentDidMount(){
    //Jarvis.say("Buenos días. ¿cómo está usted?, bienvenido al trámite del Sisben",{lang:"es-ES"})
    Jarvis.say(INSTRUCCIONES[contador].texto,{lang:"es-ES"});
  }

  componentWillUnmount(){
    Jarvis.shutUp();
  }

  setRef = webcam => {
    this.webcam = webcam;
  };

  refreshState(){
    if(contador<INSTRUCCIONES.length-1){
      contador = contador+1;
      this.setState({
        titulo:INSTRUCCIONES[contador].titulo,
        texto: INSTRUCCIONES[contador].texto,
      });

      Jarvis.shutUp();
      Jarvis.say(INSTRUCCIONES[contador].texto,{lang:"es-ES"});
      
    } else{
      this.setState({completed:true});

      Jarvis.shutUp();
      Jarvis.say("Presiona sobre el botón azul para finalizar",{lang:"es-ES"});
    }
    
  }

  capture = () => {
    if(!this.state.completed){
      console.log("Tomaste una foto")
      const imageSrc = this.webcam.getScreenshot();
      this.setState({images: IMAGES.push({src:imageSrc, thumbnail: imageSrc, thumbnailWidth:256, caption:this.state.titulo, tags: [{value: this.state.titulo, title: "cedula"}],})})
      //IMAGES.push({src:imageSrc, thumbnail: imageSrc, thumbnailWidth:256, thumbnailHeight:144, caption:"Foto", tags: [{value: "Documento", title: "cedula"}],})
      console.log(IMAGES)
      this.refreshState();
    }else{
      console.log("terminado");
      this.props.history.push('/final');
    }
    
  };

  render(){
    const videoConstraints = {
      width: 1280,
      height: 720,
      facingMode: { exact: "environment" },//{ exact: "environment" }//"user"
    };

    return (
      <div className="container " style={{textAlign:"center"}}>
        <h1>{this.state.titulo}</h1>
        <p>{this.state.texto}</p>
        <div className="container-fluid mb-5">
          <Webcam
            audio={false}
            //height={"100%"}
            ref={this.setRef}
            screenshotFormat="image/jpeg"
            width={"100%"}
            videoConstraints={videoConstraints}
            mirrored = {true}
          />
          <small id="ayudacedula" className="form-text text-muted mb-4">Presiona sobre el botón  rojo para tomar la fotografía del documento</small>
          <button type="button" 
            className="btn btn-primary" 
            onClick={this.capture} 
            style={this.state.completed? {}:{width:70, height: 70, borderRadius: "50%", backgroundImage: `url(${Background})`, backgroundSize: "cover"}}>
          {this.state.completed? "Finalizar":""}
          </button>
        </div>

        <div className="container-fluid " >
          {/*this.state.imageSrc*/}
          <Gallery images={[...IMAGES]} backdropClosesModal={true} imageCountSeparator={" de "}/>
        </div>

        
        
      </div>
    );
  }
  
    
};

export default WebcamCapture;
  
  // https://www.npmjs.com/package/react-webcam
  