import React, { useState } from "react";
import {Link} from "react-router-dom";
import { useHistory } from "react-router-dom";
import Artyom from 'artyom.js';
const Jarvis = new Artyom();
let inputField = "";

class Form extends React.Component{

    constructor (props, context){
        super(props, context);
  
        // Add `this` context to the handler functions
        this.StartAssistant = this.StartAssistant.bind(this);
        this.StopAssistant = this.StopAssistant.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChangeInput = this.handleChangeInput.bind(this);
        this.handleFocusInput = this.handleFocusInput.bind(this);

        // Prepare simple state
        this.state = {
            User: "",
            isDictating:false,
            buttonClassActive: "btn btn-warning",
            buttonClassDisable: "btn btn-danger",
            isAnyActive: false,
            inputField:""
        };

     
    }

    componentDidMount(){
        Jarvis.say("Bríndenos sus datos por favor. Antes de empezar con su trámite, necesitamos que nos diga los siguientes datos",{lang:"es-ES"})
    } 

    componentWillUnmount(){
        Jarvis.shutUp();
    }

    User = "";
    handleSubmit(evt) {
        evt.preventDefault();
        localStorage.setItem("name", this.User);
        this.props.history.push('/instrucciones');
    }

    handleChangeInput(e) {
        const inputValue = e.target.value;
        this.User = inputValue;
    }

    handleFocusInput(text){
        Jarvis.shutUp();
        Jarvis.say(text,{lang:"es-ES"});
    }

    UserDictation = Jarvis.newDictation({
        continuous:true, // Enable continuous if HTTPS connection
        lang: "es-ES",
        onResult:function(text){
            // Do something with the text
            console.log(text);
            
            if(text!=""){document.getElementById(inputField).value = text;}
            
        },
        onStart:function(){
            console.log("Puedes empezar a dictar");
        },
        onEnd:function(){
            console.log("Se ha desactivado el reconocimiento de voz");
        }
    });

    StartAssistant( valueinput ){
        let _this = this;

        if(inputField == valueinput && this.state.isDictating){
            this.UserDictation.stop();
            this.setState({
                isDictating:false,
                //buttonClass:"btn btn-danger",
                inputField:""
            });
            inputField = ""
        } else if(!this.state.isDictating){
            this.UserDictation.start();
            this.setState({
                isDictating:true,
                //buttonClass: "btn btn-warning",
                inputField: valueinput
            });
            inputField = valueinput
        } else if(inputField != valueinput && this.state.isDictating){
            inputField = valueinput
            this.setState({
                inputField: valueinput
            })
        }

        /*if(this.state.isDictating){
            this.UserDictation.stop();
            this.setState({
                isDictating:false,
                buttonClass:"btn btn-danger",
                //inputField:""
            });
            inputField = ""
        }
        else{
            this.UserDictation.start();
            this.setState({
                isDictating:true,
                buttonClass: "btn btn-warning",
                //inputField:inputField
            });
            inputField = valueinput
        }*/
        
    }

    StopAssistant(){

        this.UserDictation.stop();
        let _this = this;
        Jarvis.fatality().then(() => {    
        }).catch((err) => {
            console.error("Oops, ha ocurrido un error!", err);
        });    
    }

    render(){

        const {input} = this.state;
        let micro = ""
        if(!this.state.isDictating){
            micro = <img src={require("../images/micro2.webp")} height="20" alt="USA flag"/>
        }else{
            micro = <img src={require("../images/microred.png")} height="20" alt="USA flag"/>
        }
        return (
            <div className="container " style={{textAlign:"left"}}>
                <div style={{textAlign:"center"}}>
                    <h2>Brindenos sus datos por favor</h2>
                    <small id="ayudacedula" className="form-text text-muted">Antes de empezar con su trámite, necesitamos que nos diga los siguientes datos</small>
                    <br></br>
                </div>
                
                <form action="/instrucciones" onSubmit={this.handleSubmit}>
                    <div className="form-group">
                        <label htmlFor="nombres" className="font-weight-bold">Nombres:</label>
                        <div className="input-group text-align:center">   
                            <input type="text" className="form-control" id="nombres" placeholder="Escriba sus nombres" name="nombres"  onChange={this.handleChangeInput} onFocus={()=>this.handleFocusInput("Por favor escriba su nombre o presione sobre el botón rojo de la derecha y pronúncielo")} required />
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="nombres"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("nombres")}>
                                    <img src={this.state.inputField=="nombres"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                    </div>
    
                    <div className="form-group">
                        <label htmlFor="apellidos" className="font-weight-bold">Apellidos:</label>
                        <div className="input-group text-align:center">   
                            <input type="text" className="form-control" id="apellidos" placeholder="Escriba sus apellidos" name="apellidos" required onFocus={()=>this.handleFocusInput("Por favor escriba su apellido o presione sobre el botón rojo de la derecha y pronúncielo")}/>
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="apellidos"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("apellidos")}>
                                    <img src={this.state.inputField=="apellidos"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                        
                    </div>
    
                    <div className="form-group">
                        <label htmlFor="cedula" className="font-weight-bold">Número de cédula:</label>
                        <div className="input-group text-align:center">   
                            <input type="number" className="form-control" id="cedula" placeholder="Escriba el número de su cédula" name="cedula" onFocus={()=>this.handleFocusInput("Por favor escriba su número de cédula o presione sobre el botón rojo de la derecha y pronúncielo")} required/> 
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="cedula"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("cedula")} disabled={true}>
                                    <img src={this.state.inputField=="cedula"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div>
                        
                        <small id="ayudacedula" className="form-text text-muted">Por favor ingrese el número de documento SIN signos de puntuación.</small>
                    </div>
    
                    <div className="form-group">
                        <label htmlFor="celular" className="font-weight-bold">Número de celular:</label>
                        <div className="input-group text-align:center">   
                            <input type="number" className="form-control" id="celular" placeholder="Escriba su número de celular" name="celular" onFocus={()=>this.handleFocusInput("Por favor escriba su número de celular o presione sobre el botón rojo de la derecha y pronúncielo")} required/>
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="celular"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("celular")} disabled={true}>
                                    <img src={this.state.inputField=="celular"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                        
                        <small id="ayudacedula" className="form-text text-muted">Por favor ingrese el número de celular SIN signos de puntuación ni espacios.</small>
                    </div>
    
                    <div className="form-group">
                        <label htmlFor="direccion" className="font-weight-bold">Dirección de domicilio:</label>
                        
                        <div className="input-group text-align:center">   
                            <input type="text" className="form-control" id="direccion" placeholder="Escriba la dirección de su domicilio" name="direccion" onFocus={()=>this.handleFocusInput("Por favor escriba la dirección de su residencia o presione sobre el botón rojo de la derecha y pronúnciela")} required/>
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="direccion"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("direccion")}>
                                    <img src={this.state.inputField=="direccion"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                    </div>
    
                    <button type="submit" className="btn btn-primary">  Guardar y Continuar  </button>
                </form>
            </div>
        );
    }

    
};

export default Form;