import React from "react";
import {Link} from "react-router-dom";

import Artyom from 'artyom.js';
const Jarvis = new Artyom();

class Start extends React.Component {

    constructor (props, context){
        super(props, context);

        // Prepare simple state
        this.state = {
            isAutorized: false
        };

     
    }
  
    componentDidMount(){
        //Jarvis.say("Buenos días. ¿cómo está usted?, bienvenido al trámite del Sisben",{lang:"es-ES"})
    } 

    componentWillUnmount(){
        Jarvis.shutUp();
    }

    start(){
        Jarvis.say("Buenos días. ¿cómo está usted?, bienvenido al trámite del Sisben",{lang:"es-ES"});
        this.setState({isAutorized:true});
    }

    render(){
        if(this.state.isAutorized){
            return (
                <div className="container-fluid vh-100">
                    <h1>Buenos días</h1>
                    <p>¿cómo está usted?, bienvenido al trámite del Sisben</p>
                    <div style={{display:"flex", flexDirection:"column", alignItems:"center", margin:"2em"}}>
                        <img src={require("../images/sisben.png")} width="90%"  className="d-inline-block align-top" alt=""/>
                        <Link to={{
                            pathname:'/formulario',
                            }} style={{textDecoration:'none'}}><button type="button" className="btn btn-primary">Continuar</button></Link> 
                    </div>
                             
                </div>      
            );
        }else{
            return (
                <div className="container-fluid vh-100">
                    <p>Trámite del SISBEN</p>
                    <div style={{display:"flex", flexDirection:"column", alignItems:"center", margin:"2em"}}>
                        <img src={require("../images/logo.jpg")} width="90%"  className="d-inline-block align-top" alt=""/>
                        <button type="button" onClick={this.start.bind(this)} className="btn btn-primary">Comenzar</button>
                    </div>
                             
                </div>      
            );
        }
        
    }
};

export default Start;